import React, { useState, useEffect } from "react";
import ApiGet from "../Api/Get";
import ApiPost from "../Api/Post";

const useUserFrom = () => {
  var today = new Date();
  var day = today.toLocaleDateString("en-US", { day: "numeric" });
  var month = today.toLocaleDateString("en-US", { month: "long" });
  var year = today.toLocaleDateString("en-US", { year: "numeric" });
  var currentDate = day + " " + month + " " + year;
  const [data, setData] = useState({ usersList: [] });
  const [toAdd, setToAdd] = useState(false);
  const [toOpen, setToOpen] = useState(false);
  const [toOpenSortby, setToSortby] = React.useState(null);
  const [sortOption, setSortOption] = React.useState();
  const handleSortOptionChange = (event) => {
    setSortOption(event.target.value);
  };
  const [values, setValues] = useState({
    userId: "",
    toEdit: false,
  });
  //TO DO
  useEffect(() => {
    getUsersList();
  }, []);
  async function getUsersList() {
    
    let response = await fetch(ApiGet.getAdmin, {
      headers: {
        "content-type": "application/json",
        authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
    });

    let responseJson = await response.json();
    setData({
      usersList: responseJson.data.data,
    });
  }
 
  const handleClickEdit = (id) => {
    setValues({
      userId: id,
      toEdit: true,
    });
    sessionStorage.setItem("userId", id);
  };

  const handleClickOpen = (id) => {
    setValues({
      userId: id,
    });
    setToOpen(true);
  };
  const handleClose = () => {
    setToOpen(false);
  };
  const handleOpenSortby = (event) => {
    setToSortby(event.currentTarget);
  };
  const handleCloseSortby = (event) => {
    setToSortby(null);
  };
  const handleAddUser = () => {
    sessionStorage.removeItem("userId");
    setToAdd(true);
  };
  const handleDelete = () => {
    deleteUser();
  };
  async function deleteUser() {
    let response = await fetch(
      ApiPost.deleteUser + "/" + values.userId,
      {
        method: "DELETE",
        headers: {
          "content-type": "application/json",
          authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
      }
    );
    let responseJson = await response.json();
    
    if (responseJson.code == "ADMINISTRATOR_DELETED") {
      setTimeout(() => {
        getUsersList();
      }, 100);
    }
    setToOpen(false);
  }
  return {
    currentDate,
    toOpen,
    toAdd,
    values,
    toOpenSortby,
    data,
    handleAddUser,
    handleClickEdit,
    handleClickOpen,
    handleClose,
    handleOpenSortby,
    handleCloseSortby,
    sortOption,
    handleSortOptionChange,
    handleDelete,
  };
};

export default useUserFrom;
