import React, { useState, useEffect } from "react";
import { useFormik } from "formik";
import ApiPost from "../Api/Post";
import ApiGet from "../Api/Get";

const useAddUserForm = () => {
  const [toEdit, setToEdit] = useState(false);
  const [passwordError, setPasswordError] = useState(false);
  const formik = useFormik({
    initialValues: {
      firstName: "",
      lastName: "",
      phoneNumber: "",
      email: "",
      password: "",
      type: "administrator",
    },
    onSubmit: (values) => {
      if(values.password !== "" && validatePassword(values.password)) {
        if (toEdit === false) {
          handleAddUser(values);
        } else {
          handleUpdateUser(values);
        }
      } else {
        setPasswordError(true);
      }
    },
  });
  useEffect(() => {
    var userid = sessionStorage.getItem("userId");
    if (userid !== null) {
      setToEdit(true);
      getUserDetails(userid);
    }
  }, []);
  async function getUserDetails(id) {
    const response = await fetch(ApiGet.getAdmin + "/" + id, {
      headers: {
        "content-type": "application/json",
        authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
    });
    const responseJson = await response.json();
    formik.setFieldValue("firstName", responseJson.data.first_name);
    formik.setFieldValue("lastName", responseJson.data.last_name);
    formik.setFieldValue("phoneNumber", responseJson.data.phone);
    formik.setFieldValue("email", responseJson.data.email);
    formik.setFieldValue("password", responseJson.data.password);
    formik.setFieldValue("type", responseJson.data.type);
  }
  async function handleAddUser(values) {
    const response = await fetch(ApiPost.CreateAdmin, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
      body: JSON.stringify({
        first_name: values.firstName,
        last_name: values.lastName,
        phone: values.phoneNumber,
        email: values.email,
        password: values.password,
        type: values.type,
        active: true,
      }),
    });
    const responseJson = await response.json();
    
    if(responseJson.code === "ADMINISTRATOR_CREATED") {
      setToOpen(true);
    } else {
      //alert(responseJson.code);
    }
  }
  async function handleUpdateUser(values) {
    const userId = sessionStorage.getItem("userId");
    const response = await fetch(ApiPost.UpdateAdmin + "/" + userId, {
      method: "PUT",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
      body: JSON.stringify({
        first_name: values.firstName,
        last_name: values.lastName,
        phone: values.phoneNumber,
        email: values.email,
        password: values.password,
        type: values.type,
      }),
    });
    debugger;
    const responseJson = await response.json();
    alert(responseJson.code);
    setToOpen(true);
  }
  const [toOpen, setToOpen] = useState(false);
  const [toUsersList, setToUsersList] = useState(false);
  const handleBack = () => {
    setToUsersList(true);
  };
  const handleClickOpen = () => {
    setToOpen(true);
  };
  const handleClose = () => {
    setToOpen(false);
  };
  const handleAdministrator = () => {
    formik.setFieldValue("type", "administrator");
  };
  const handleModerator = () => {
    formik.setFieldValue("type", "moderator");
  };
  function validatePassword(password) {
    const regEx = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{8,}$/;
    if (!password.match(regEx)) {
      return false; //Password does not match regular expression
    }
    return true; //Password matches regular expression
  }
  return {
    formik,
    toEdit,
    toOpen,
    toUsersList,
    passwordError,
    handleBack,
    handleClickOpen,
    handleClose,
    handleAdministrator,
    handleModerator,
  };
};

export default useAddUserForm;
