import React, { useState, useEffect } from "react";
import ApiGet from "../Api/Get";
import ApiPost from "../Api/Post";

const tabValue = 1;
const useNurseForm = () => {
  const [toOpen, setToOpen] = useState(false);
  const [toAdd, setToAdd] = useState(false);
  const [toEdit, setToEdit] = useState(false);
  const [toOpenSortby, setToSortby] = useState(null);
  const [toOpenExport, setToExport] = useState(null);
  const [sortOption, setSortOption] = useState();
  const handleSortOptionChange = (event) => {
    setSortOption(event.target.value);
  };
  const [value, setValue] = React.useState(tabValue);
  const handleTabChange = (event, newValue) => {
    setValue(newValue);
  };
  const [values, setFieldValue] = useState({
    nurseId: "",
    fname: false,
    lname: false,
    certifications: false,
    credentials: false,
    startDate: false,
    email: false,
    hospital: false,
    title: false,
    experience: false,
  });
  const [data, setData] = useState({
    nurseList: [ ],
  });
  const handleAddNurse = () => {
    setToAdd(true);
    sessionStorage.removeItem("staffId");
  };
  const handleClickOpen = (id) => {
    setFieldValue({
      nurseId: id,
    });
    setToOpen(true);
  };
  const handleClickEdit = (id) => {
    sessionStorage.setItem("staffId", id);
    setToEdit(true);
  };
  const handleClose = () => {
    setToOpen(false);
  };
  const handleOpenSortby = (event) => {
    setToSortby(event.currentTarget);
  };
  const handleCloseSortby = (event) => {
    setToSortby(null);
  };
  const handleOpenExport = (event) => {
    setToExport(event.currentTarget);
  };
  const handleCloseExport = (event) => {
    setToExport(null);
  };
  const handleChange = (event) => {
    setFieldValue({ ...values, [event.target.name]: event.target.checked });
  };
  //TO DO
  useEffect(() => {
    getStaffs();
  },[]);
  async function getStaffs() {
    
    const hospitalId = sessionStorage.getItem("hospitalId");
    const response = await fetch(ApiGet.getStaff + "/" + hospitalId+"/staff", {
      method: "GET",
      headers: {
        Accept: "application/json",
        authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
    });
    const responseJson = await response.json();
    setData({nurseList:responseJson.data.data});
  }
  async function deleteStaff() {
   let id = sessionStorage.getItem("hospitalId");
    let response = await fetch(
      ApiPost.deleteStaff + "/" +id+"/"+"staff/"+values.nurseId,
      {
        method: "DELETE",
        headers: {
          "content-type": "application/json",
          authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
      }
    );
    let responseJson = await response.json();
    if (responseJson.code == "HOSPITAL_STAFF_DELETED") {
      setTimeout(() => {
        getStaffs();
      }, 100);
    }
    setToOpen(false);
  }
  const handleDelete = () => {
    deleteStaff();
  };
  return {
    data,
    toOpen,
    toAdd,
    toEdit,
    sortOption,
    toOpenSortby,
    toOpenExport,
    values,
    value,
    handleClickOpen,
    handleClickEdit,
    handleClose,
    handleOpenSortby,
    handleCloseSortby,
    handleCloseExport,
    handleOpenExport,
    handleAddNurse,
    handleSortOptionChange,
    handleChange,
    handleTabChange,
    handleDelete
  };
};

export default useNurseForm;
