import React, { useState, useRef, useEffect } from "react";
import { useFormik } from "formik";
import ApiPost from "../Api/Post";
import ApiGet from "../Api/Get";

const useAddNurseForm = (callback) => {
  const [toEdit, setToEdit] = useState(false);
  const [toBack, setToBack] = useState(false);
  const [toCustomerSupportRequest, setToCustomerSupportRequest] = useState(
    false
  );
  const [popup, setToPopup] = useState(false);
  const [toRemove, setToRemove] = useState(false);
  const [unitData, setUnitData] = useState([]);
  const [toOpenSuspend, setOpenSuspend] = useState(false);
  const imageInput = useRef(null);
  const [image, setImage] = useState("");
  const [showUnits, setShowUnits] = useState(false);
  const [notifications, setNotifications] = useState(false);
  const [unitDetails, setUnitDetails] = useState({
    unitsDetails: [],
  });
  const [selectedUnit, setSelectedUnits] = useState([]);
  const [selectedUnitId, setSelectedUnitId] = useState(0);
  const formik = useFormik({
    initialValues: {
      firstName: "",
      lastName: "",
      email: "",
      phoneNumber: "",
      title: "",
      certifications: "",
      experience: "",
      hospitalName: sessionStorage.getItem("hospitalName"),
      startDate: "",
      units: "",
      password: "",
      role: "charge_nurse",
    },
    onSubmit: (values) => {

      if (toEdit === false) {
        handleAddStaff(values);
      } else {
        handleupdateStaff(values);
      }
    },
    onReset: (values) => {},
  });

  useEffect(() => {
    var staffId = sessionStorage.getItem("staffId");
    if (staffId != null) {
      setToEdit(true);
      getStaff(staffId);
    }
    getUnits();
  }, []);
  async function getStaff(staffId) {
    let id = sessionStorage.getItem("hospitalId");
    let response = await fetch(
      ApiGet.getStaffDetails + "/" + id + "/staff" + "/" + staffId,
      {
        headers: {
          Accept: "application/json",
          authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
      }
    );
    let responseJson = await response.json();
    formik.setFieldValue("firstName", responseJson.data.user.first_name);
    formik.setFieldValue("lastName", responseJson.data.user.last_name);
    formik.setFieldValue("email", responseJson.data.user.email);
    formik.setFieldValue("phoneNumber", responseJson.data.user.phone);
    formik.setFieldValue("title", responseJson.data.title);
    formik.setFieldValue("certifications", responseJson.data.certifications);
    formik.setFieldValue("experience", responseJson.data.experience);
    formik.setFieldValue("role", responseJson.data.roles);
    setNotifications(responseJson.data.sms_notifications);
    setImage(responseJson.data.image);
    setUnitData(responseJson.data.units);
    setShowUnits(true);
    var selectedUnitList = responseJson.data.units;
    var ids = selectedUnitList.map((item) => item.id);
    setSelectedUnits(ids);
    var date = new Date(responseJson.data.start_date),
      month = "" + (date.getMonth() + 1),
      day = "" + date.getDate(),
      year = date.getFullYear();
    if (month.length < 2) month = "0" + month;
    if (day.length < 2) day = "0" + day;
    var startDate = year + "-" + month + "-" + day;
    formik.setFieldValue("startDate", startDate);
    
  }
  const handleClose = () => {
    setToPopup(false);
  };
  const handleClickSuspend = () => {
    setOpenSuspend(true);
  };
  const handleClickChargeNurse = () => {
    setToPopup(true);
  };
  const handleCloseSuspend = () => {
    setOpenSuspend(false);
  };
  const handleClickCustomerRequest = () => {
    setToCustomerSupportRequest(true);
  };
  // Function to handle profile uploads
  const handleImageClick = (e) => {
    imageInput.current.click();
  };
  const handleImageChange = (e) => {
    e.preventDefault();
    const reader = new FileReader();
    const img = e.target.files[0];
    if (img) {
      reader.readAsDataURL(img);
    }
    reader.onloadend = () => {
      setImage(reader.result);
    };
  };
  const handleDelete = (unitToDelete) => () => {
    setUnitData((units) => units.filter((item) => item.id !== unitToDelete));
    setSelectedUnits(selectedUnit.filter((item) => item !== unitToDelete));
  };
  const handleAddUnits = () => {
    var Units = unitDetails.unitsDetails.find((item) => {
      return item.id === selectedUnitId;
    });
    setUnitData((units) => [...units, Units]);
    setSelectedUnits((id) => [...id, selectedUnitId]);
    setShowUnits(true);
  };
  const handleBack = () => {
    setToBack(true);
  };
  const handleClickChargeNurseOk = () => {
    formik.setFieldValue("role", "charge_nurse");
    setToPopup(false);
  };
  const handleAdmin = () => {
    formik.setFieldValue("role", "admin");
  };
  const handleNone = () => {
    formik.setFieldValue("role", "none");
  };
  const handleOpenRemove = () => {
    setToRemove(true);
  };
  const handleCloseRemove = () => {
    setToRemove(false);
  };
  const handleClickRemoveUser = () => {
    RemoveStaff();
  };
  const handleNotificationsChange = (event) => {
    setNotifications(event.target.checked);
  };
  async function handleAddStaff(values) {
    const id = sessionStorage.getItem("hospitalId");
    var date = new Date(values.startDate),
      month = "" + (date.getMonth() + 1),
      day = "" + date.getDate(),
      year = date.getFullYear();
    if (month.length < 2) month = "0" + month;
    if (day.length < 2) day = "0" + day;
    var startDate = year + "-" + month + "-" + day;
    const response = await fetch(ApiPost.insertStaff + "/" + id + "/staff", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
      body: JSON.stringify({
        first_name: values.firstName,
        last_name: values.lastName,
        email: values.email,
        phone: values.phoneNumber,
        password: values.password,
        type: "hospital_staff",
        roles: values.role,
        profile_picture: image,
        title: values.title,
        certifications: values.certifications,
        sms_notifications: notifications,
        active: true,
        experience: values.experience,
        start_date: startDate,
        units: selectedUnit,
      }),
    });
    const responseJson = await response.json();
    
    
    if (responseJson.code === "HOSPITAL_STAFF_CREATED") {
      setTimeout(() => {
        setToBack(true);
      }, 100);
    }
  }
  async function handleupdateStaff(values) {
  
    const hospitalId = sessionStorage.getItem("hospitalId");
    const staffId = sessionStorage.getItem("staffId");
    var date = new Date(values.startDate),
      month = "" + (date.getMonth() + 1),
      day = "" + date.getDate(),
      year = date.getFullYear();
    if (month.length < 2) month = "0" + month;
    if (day.length < 2) day = "0" + day;
    var startDate = year + "-" + month + "-" + day;
    const response = await fetch(
      ApiPost.updateStaff + "/" + hospitalId + "/staff" + "/" + staffId,
      {
        method: "PUT",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
        body: JSON.stringify({
          first_name: values.firstName,
          last_name: values.lastName,
          email: values.email,
          phone: values.phoneNumber,
          password: values.password,
          type: "hospital_staff",
          roles: values.role,
          profile_picture: image,
          title: values.title,
          certifications: values.certifications,
          sms_notifications: notifications,
          active: true,
          experience: values.experience,
          start_date: startDate,
          units: selectedUnit,
        }),
      }
    );
    const responseJson = await response.json();
    if (responseJson.code === "HOSPITAL_STAFF_UPDATED") {
      setTimeout(() => {
        setToBack(true);
      }, 100);
    }
  }
  async function getUnits() {
    const hospitalId = sessionStorage.getItem("hospitalId");
    let response = await fetch(ApiGet.getUnits + "/" + hospitalId + "/unit", {
      headers: {
        Accept: "application/json",
        authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
    });

    let responseJson = await response.json();
    setUnitDetails({
      unitsDetails: responseJson.data.data,
    });
  }
  const handleAdminChange = (event, value) => {
    if (value !== null) {
      setSelectedUnitId(value.id);
    }
  };
  async function RemoveStaff() {
  
    let id = sessionStorage.getItem("hospitalId");
    const staffId = sessionStorage.getItem("staffId");
    let response = await fetch(
      ApiPost.deleteStaff + "/" + id + "/" + "staff/" + staffId,
      {
        method: "DELETE",
        headers: {
          "content-type": "application/json",
          authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
      }
    );
    let responseJson = await response.json();
    if (responseJson.code === "HOSPITAL_STAFF_DELETED") {
      setTimeout(() => {
        setToBack(true);
      }, 100);
    }
    setToRemove(false);
  }
  return {
    formik,
    toEdit,
    handleClose,
    popup,
    handleClickSuspend,
    handleClickChargeNurse,
    toOpenSuspend,
    handleCloseSuspend,
    handleClickCustomerRequest,
    toCustomerSupportRequest,
    imageInput,
    handleImageClick,
    handleImageChange,
    image,
    unitData,
    handleDelete,
    handleAddUnits,
    showUnits,
    toBack,
    handleBack,
    handleClickChargeNurseOk,
    toRemove,
    handleOpenRemove,
    handleCloseRemove,
    handleAdmin,
    handleNone,
    handleNotificationsChange,
    notifications,
    unitDetails,
    handleAdminChange,
    handleClickRemoveUser,
  };
};

export default useAddNurseForm;
