import React, { useState, useEffect } from "react";
import { useFormik } from "formik";
import ApiGet from "../../Api/Get";
import ApiPost from "../../Api/Post";

const UseEditUnitForm = () => {
  const formik = useFormik({
    initialValues: {
      unitName: "",
      noofRooms: "",
      unitColor: "",
      dimension1Value: "",
      dimension2Value: "",
      dimension3Value: "",
      dimension4Value: "",
      dimension5Value: "",
      dimension6Value: "",
      min1Value: "",
      min2Value: "",
      min3Value: "",
      min4Value: "",
      min5Value: "",
      min6Value: "",
      max1Value: "",
      max2Value: "",
      max3Value: "",
      max4Value: "",
      max5Value: "",
      max6Value: "",
      designation: [],
      assignPermission: [],
      clinicalRatioDayPatients: "",
      clinicalRationDayNurse: "",
      clinicalRatioNightPatients: "",
      clinicalRatioNightNurse: "",
      nurseAideRatioDayPatient: "",
      nurseAideRatioDayAide: "",
      nurseAideRatioNightPatient: "",
      nurseAideRatioNightAide: "",
    },
    onSubmit: async (values) => {
      const hospitalId = sessionStorage.getItem("hospitalId");
      const unitId = sessionStorage.getItem("UnitID");
      const response = await fetch(
        ApiPost.updateUnits + "/" + hospitalId + "/unit" + "/" + unitId,
        {
          method: "PUT",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
          },
          body: JSON.stringify({
            name: values.unitName,
            rooms: values.noofRooms,
            brand_color: values.unitColor,
            nurse_ratio_patient_day: values.clinicalRatioDayPatients,
            nurse_ratio_nurse_day: values.clinicalRationDayNurse,
            aides_ratio_patient_day: values.nurseAideRatioDayPatient,
            aides_ratio_aide_day: values.nurseAideRatioDayAide,
            nurse_ratio_patient_night: values.clinicalRatioNightPatients,
            nurse_ratio_nurse_night: values.clinicalRatioNightNurse,
            aides_ratio_patient_night: values.nurseAideRatioNightPatient,
            aides_ratio_aide_night: values.nurseAideRatioNightAide,
            designation: values.designation,
          }),
        }
      );
      const responseJson = await response.json();
      if (responseJson.code === "HOSPITAL_UNIT_UPDATED") {
        setTimeout(() => {
          setToBack(true);
        }, 100);
      }
    },
  });

  useEffect(() => {
    getStaffs();
    // getUnit();
  }, []);
  async function getUnit() {
    const unitId = sessionStorage.getItem("UnitID");
    const hospitalId = sessionStorage.getItem("hospitalId");
    let response = await fetch(
      ApiGet.getUnits + "/" + hospitalId + "/unit" + "/" + unitId,
      {
        headers: {
          "content-type": "application/json",
          authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
      }
    );
    let responseJson = await response.json();
    formik.setFieldValue("unitName", responseJson.data.name);
    formik.setFieldValue("noofRooms", responseJson.data.rooms);
    formik.setFieldValue(
      "clinicalRatioDayPatients",
      responseJson.data.nurse_ratio_patient_day
    );
    formik.setFieldValue(
      "clinicalRationDayNurse",
      responseJson.data.nurse_ratio_nurse_day
    );
    formik.setFieldValue(
      "nurseAideRatioDayPatient",
      responseJson.data.aides_ratio_patient_day
    );
    formik.setFieldValue(
      "nurseAideRatioDayAide",
      responseJson.data.aides_ratio_aide_day
    );
    formik.setFieldValue(
      "clinicalRatioNightPatients",
      responseJson.data.nurse_ratio_patient_night
    );
    formik.setFieldValue(
      "clinicalRatioNightNurse",
      responseJson.data.nurse_ratio_nurse_night
    );
    formik.setFieldValue(
      "nurseAideRatioNightPatient",
      responseJson.data.aides_ratio_patient_night
    );
    formik.setFieldValue(
      "nurseAideRatioNightAide",
      responseJson.data.aides_ratio_aide_night
    );
    formik.setFieldValue("designation", responseJson.data.designation);
    formik.setFieldValue("unitColor", responseJson.data.brand_color);
    var staffdetails = responseJson.data.staffs;
    var ids = staffdetails.map((item) => item.id);
    if (ids !== null) {
      setShowPermissionList(true);
    }
    setSelectedNurse(ids);
  }

  async function getStaffs() {
    const hospitalId = sessionStorage.getItem("hospitalId");
    const response = await fetch(
      ApiGet.getStaff + "/" + hospitalId + "/staff",
      {
        method: "GET",
        headers: {
          Accept: "application/json",
          authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
      }
    );
    const responseJson = await response.json();
    getUnit();
    setNurseData({ nurseDetails: responseJson.data.data });
  }
  function getStaffName(id) {
    var first_name = nurseData.nurseDetails.find((x) => x.id === id).user
      .first_name;
    var last_name = nurseData.nurseDetails.find((x) => x.id === id).user
      .last_name;
    var name = first_name + " " + last_name;
    return name;
  }

  const [values, setValues] = useState({
    pageValue: "Page1",
  });
  const [designationlist, setDesignationlist] = useState([]); //designationlist
  const [showdesignationList, setShowDesignationList] = useState(false); //show added designation
  const [permissionlist, setPermissionlist] = useState([]); //permissionlist
  const [showpermissionList, setShowPermissionList] = useState(false); //show added permissionlist
  const [deletemodal, setDeleteModal] = useState(false); //opendeletemodal
  const [splitmodal, setSplitModal] = useState(false); //opensplitmodal
  const [addroommodal, setAddroomModal] = useState(false); //opensplitmodal
  const [validateerrors, setValidateErrors] = useState({}); //validateerrors
  const [continueError, setContinueError] = useState(false); //set message
  const [fields, setFields] = useState([{ value: null }]); //adddynamic fields
  const [nurseData, setNurseData] = useState({ nurseDetails: [] });
  const [nurseListData, setNurseListData] = useState({ nurseListDetails: [] });
  const [selectedNurse, setSelectedNurse] = useState([]);
  const [selectedNurseId, setSelectedNurseId] = useState(0);
  const [nurseList, setNurseList] = useState([]);

  const openDeleteModal = () => setDeleteModal(true);
  const openSplitModal = () => setSplitModal(true);
  const openAddroomModal = () => setAddroomModal(true);
  const cancelDeleteModal = () => setDeleteModal(false);
  const cancelSplitModal = () => setSplitModal(false);
  const cancelAddroomModal = () => setAddroomModal(false);
  const [toBack, setToBack] = useState(false);

  //Add Designation
  const handleAddDesignation = () => {
    if (formik.values.designation !== "") {
      designationlist.push(formik.values.designation);
    }
    setShowDesignationList(true);
  };
  //Remove Desingation
  const handleRemoveDesignation = (designation) => {
    setDesignationlist((designationlist) =>
      designationlist.filter((item) => item !== designation)
    );
    setShowDesignationList(false);
  };
  //Add Permission
  const handleAddPermission = (event) => {
    event.preventDefault();
    var NurseData = nurseData.nurseDetails.find((item) => {
      return item.id === selectedNurseId;
    });

    setNurseList((nurse) => [...nurse, NurseData.user]);
    setSelectedNurse((id) => [...id, selectedNurseId]);
    // setShowPermissionList(true);
  };
  //Remove Permission
  const handleRemovePermission = (permission) => {
    setSelectedNurse(selectedNurse.filter((item) => item !== permission));
  };
  const handleNurseChange = (event, value) => {
    if (value !== null) {
      setSelectedNurseId(value.id);
    }
  };
  //Add Dimension
  function handleAddDimension() {
    const values = [...fields];
    values.push({ value: null });
    setFields(values);
  }
  //Remove Dimension
  function handleRemove(i) {
    const values = [...fields];
    values.splice(i, 1);
    setFields(values);
  }

  const handleClickColor = (color) => {
    formik.values.unitColor = color;
  };
  //Redirect page
  async function handleNext() {
    if (formik.values.unitName === "") {
      setValidateErrors({
        ...validateerrors,
        unitName: "Field Required!",
        Valid: true,
      });
    } else {
      setContinueError(true);
      setValues({ pageValue: "Page2" });
    }
  }

  //Redirect page
  async function handleBack() {
    setValues({ pageValue: "Page1" });
  }
  const UnitsList = () => {
    setToBack(true);
  };
  return {
    values,
    deletemodal,
    splitmodal,
    formik,
    addroommodal,
    continueError,
    validateerrors,
    designationlist,
    showdesignationList,
    permissionlist,
    showpermissionList,
    fields,
    toBack,
    nurseData,
    selectedNurse,
    selectedNurseId,
    nurseList,
    nurseListData,
    handleNext,
    openDeleteModal,
    cancelDeleteModal,
    openSplitModal,
    cancelSplitModal,
    openAddroomModal,
    cancelAddroomModal,
    handleBack,
    handleAddDesignation,
    handleRemoveDesignation,
    handleAddPermission,
    handleRemovePermission,
    handleAddDimension,
    handleRemove,
    UnitsList,
    handleClickColor,
    handleNurseChange,
    getStaffName,
  };
};
export default UseEditUnitForm;
